body {
  margin: 0;
}

* {
  box-sizing: border-box;
}

.card {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  width: 200px;
  padding: 16px;
  transition-duration: .3s;
  display: flex;
}

.card__title {
  width: 166px;
  margin: 40px 0 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  transition-duration: .3s;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__title {
  color: #34568b;
}

.card__logo {
  width: 160px;
  height: 134px;
  margin-top: 16px;
}

.card__subtitle {
  color: #616070;
  margin: 4px auto 0 0;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  line-height: 14px;
}

.card__rating {
  align-items: end;
  width: 166px;
  margin-top: 16px;
  line-height: 0;
  display: flex;
}

.card__text-rating {
  color: #060b35;
  margin-block: 0;
  margin-left: auto;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  width: 166px;
  margin-top: 24px;
  display: flex;
}

.card__text-price {
  color: #616070;
  margin-block: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  line-height: 18px;
}

.card__count {
  color: #060b35;
  margin-block: 0;
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border: 1px solid #0000;
  border-radius: 5px;
  width: 166px;
  height: 40px;
  margin-top: 16px;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  transition-duration: .3s;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}
